@import url(https://fonts.googleapis.com/css?family=Racing+Sans+One);
html,body{padding:15px;margin:0;height:100%;width:100%;background-color:#222;font-family:sans-serif;text-align:center}*{box-sizing:border-box}#root{--padding: 15px;--gap: var(--padding);min-height:100%;width:100%;display:flex;justify-content:center;align-items:center}#root ul,#root li,#root a{all:unset}#root h2{font-size:9vmin;margin:0;margin-bottom:var(--padding);text-align:Left;font-family:"Racing Sans One", "Lato", "Lucida Grande", "Lucida Sans Unicode", Tahoma, Sans-Serif;color:#fff;text-shadow:0px 1px 0px #999, 0px 2px 0px #888, 0px 3px 0px #777, 0px 4px 0px #666, 0px 5px 0px #555, 0px 6px 0px #444, 0px 7px 0px #333, 0px 8px 7px #001135}#root nav{text-align:Left;padding:15px 0}#root nav a{color:#e9e9e9;border:solid 1px #555;display:inline-block;padding:5px;padding:var(--padding);margin-right:var(--padding);background-color:white;box-sizing:border-box;text-shadow:0 1px 1px rgba(0,0,0,0.3);border-radius:0.5em;box-shadow:0 1px 2px rgba(0,0,0,0.2);background:linear-gradient(0deg, #888 0%, #575757 100%);transform:scale(0.95);transition:0.2s ease-in-out}#root nav a:hover{transform:scale(1)}#root section{width:100%;max-width:800px;border:1px solid black;padding:var(--padding);display:inline-block;border:1px solid #999;margin:auto;padding:2vmin;background:#666;box-shadow:0 1px 0 black,inset 0 1px 7px rgba(0,0,0,0.4)}#root ul{display:grid;grid-gap:var(--gap);grid-template-columns:repeat(auto-fit, minmax(175px, 1fr));justify-content:center;align-items:center}#root li{border-radius:15px;text-align:center;height:100%;width:100%;box-sizing:border-box}#root li a{cursor:pointer;display:block;padding:var(--padding);background-color:white;height:100%;width:100%;box-sizing:border-box;color:black;display:flex;justify-content:center;align-items:center;text-shadow:0 1px 1px rgba(0,0,0,0.3);border-radius:0.5em;box-shadow:0 1px 2px rgba(0,0,0,0.2);transform:scale(0.95);transition:0.2s ease-in-out}#root li a:hover{transform:scale(1)}

